import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      component: resolve => require(['@/views/login'], resolve)
    },
    {
      path: '/main',
      component: resolve => require(['@/views/main'], resolve),
      children: [
        {
          path: '/statistical/dataSummary',
          component: resolve => require(['@/views/statistical/dataSummary_list'], resolve)
        },
        {
          path: '/statistical/seat',
          component: resolve => require(['@/views/statistical/seat'], resolve)
        },
        {
          path: '/statistical/internal_operations',
          component: resolve => require(['@/views/statistical/internal_operations'], resolve)
        },
        {
          path: '/statistical/no_single_cause',
          component: resolve => require(['@/views/statistical/no_single_cause'], resolve)
        },
        {
          path: '/statistical/perCapita',
          component: resolve => require(['@/views/statistical/perCapita'], resolve)
        },
        {
          path: '/statistical/detailReport',
          component: resolve => require(['@/views/statistical/detailReport'], resolve)
        },
        {
          path: '/dataprovide/jobLog',
          component: resolve => require(['@/views/dataprovide/jobLog'], resolve)
        },
        {
          path: '/dataprovide/proportion',
          component: resolve => require(['@/views/dataprovide/proportion'], resolve)
        },
        {
          path: '/dataprovide/getProTab',
          component: resolve => require(['@/views/dataprovide/getProTab'], resolve)
        },
        {
          path: '/workplace/seatEffect',
          component: resolve => require(['@/views/workplace/seatEffect'], resolve)
        },

        {
          path: '/workplace/employee',
          component: resolve => require(['@/views/workplace/employee'], resolve)
        },
        {
          path: '/workplace/seat_ability',
          component: resolve => require(['@/views/workplace/seat_ability'], resolve)
         },


      ]
    }
  ]
})



