//获取数据批次
export function get_model(){
  return ajax.request({
    methods: 'get',
    url: 'public',
    params:{
      type:'model'
    }
  }).then((res) => {
    if(res.code == '200'){
      var data ={
        id: 0,
        memo: 'unknown'
      }
     // Vue.set(res.data, 0, data);
      res.data.push(data);
      localStorage.setItem('model', JSON.stringify(res.data));
    }
  }).catch((error) => {
    console.log(error);
  })
}
