//内部运营请求数据
export function operation(dateline,proj,page,size) {
  return ajax.request({
    method: 'get',
    url: 'operation',
    params:{
      dateline:format(dateline, "yyyyMMdd"),
      proj:proj,
      page:page,
      size:size
    }
  });
}
//内部运营数据修改
export function operationEdit(dateline, proj, goal, measures, sumday, page, size){
  return ajax.request({
    method: 'post',
    url: 'operation',
    params:{
      dateline: format(dateline, "yyyyMMdd"),
      proj: proj,
      goal: goal,
      measures: measures,
      sumday: sumday,
      page: page,
      size: size
    }
  })
}
export function employee(corp, proj, page, size){
  return ajax.request({
    method: 'get',
    url: 'employee',
    params:{
      corp: corp,
      proj: proj,
      page: page,
      size: size
    }
  })
}
//职场人员文件上传
export function employeeFile(file){
  return ajax.request({
    method: 'post',
    url: 'employee',
    params:{
      file: file
    },
    headers: {
      'Content-Type': 'multipart/form-data'
    }
  })
}

//坐席功效请求
export function seatEffect(beginCalldate, endCalldate, corp, prjid_local, orderbyCondition, orderbyFlag, page, size){
  return ajax.request({
    method: 'post',
    url: 'userstat',
    params:{
      beginCalldate: format(beginCalldate, "yyyyMMdd"),
      endCalldate: format(endCalldate, "yyyyMMdd"),
      corp: corp,
      prjid_local: prjid_local,
      orderbyCondition: orderbyCondition,
      orderbyFlag: orderbyFlag,
      page: page,
      size: size
    }
  })
}
//数据提取 根据外呼团队获取外呼项目
export function get_datacmemo(cmemo, getUrl) {
  return ajax.request({
    method: 'get',
    url: getUrl,
    params:{
      cmemo: cmemo
    }
  });
}

//根据项目id获取地域及模型
export function get_area(prjid_local){
  return ajax.request({
    method: 'get',
    url: 'area',
    params:{
      prjid_local: prjid_local
    }
  })
}





